<template>
    <div class="box">
        <div class="chatWindow" v-if="openChat" :class="{vievedTrue: !chat.viewed, fullWindowChat: fullWindowChat}">
            <!-- ACTIONS -->
            <button class="close" v-on:click="(openChat = false) && (showUsersList = false)">X</button>
            <button class="leaveChanel" v-on:click="leavechanel()" v-if="joinChannel || createChannel || showActions"> <font-awesome-icon icon="running" title="leave channel"/></button>
            <span v-if="showActions">
                <button class="share" v-on:click="sharechanel()" v-if="Channel != ''"> <font-awesome-icon icon="share" title="Share link to channel"/></button>
                <button class="expand" v-on:click="fullWindowChat = !fullWindowChat" v-if="Channel != ''"> <font-awesome-icon icon="expand"/></button>
                <button class="usersListButton" v-on:click="showUsersList = !showUsersList" v-if="Channel != ''"> {{usersList.length -1}} <font-awesome-icon icon="users"/></button>
            </span>   
            <!-- /ACTIONS -->
            <!-- USERS LIST -->
            <span class="chatWindow__usersList" :class="{showUsersList: showUsersList}">
                <b>Users list</b> <br><br>
                <span v-for="user in usersList" :key="user" class="chatWindow__usersList__user">
                    <span v-if="(user != 'System') && (user != chat.user)" v-on:click="privateMessage(user)" title="click to send private message">{{user}} <br></span>
                    <span v-if="user == chat.user"><b> You </b> <br></span> 
                </span>
            </span>
            <!-- JOIN Channel FORM -->
            <div :class="{showJoinChannel: joinChannel}" class="chatWindow__joinChannel">
                <h1 class="chatWindow__header">Join Channel </h1>
                <div class="chatWindow__form">
                    <div v-if="!chatNameFromUrl">
                        <span> Channel name </span> <br>
                        <input type="text" v-model="chat.channel" class="chatWindow__form--input">
                    </div>
                    <div>
                        <span> Your name  </span> <br>
                        <input type="text" v-model="chat.user" class="chatWindow__form--input">
                    </div>
                    <button v-on:click="JoinChannel()" class = "chatWindow__buttons__action" > Join Channel</button>
                </div>
            </div>
            <!-- /JOIN Channel FORM -->
            <!-- CREATE Channel FROM -->
            <div :class="{showCreateChannel: createChannel}" class="chatWindow__createChannel">
                <h1 class="chatWindow__header">Create Channel </h1>
                <div class="chatWindow__form">
                    <div>
                        <span> Channel name </span> <br>
                        <input type="text" v-model="chat.create" class="chatWindow__form--input">
                    </div>

                    <button v-on:click="CreateChannel()" class = "chatWindow__buttons__action"> Create Channel</button>
                </div>
            </div>
            <!-- /CREATE Channel FROM -->
            <!-- START BUTTONS -->
            <div class="chatWindow__buttons" v-if="!createChannel && !joinChannel && !ShowChat">
                <button class = "chatWindow__buttons__action" v-on:click="joinChannel = !joinChannel"> JOIN Channel </button>
                <button class = "chatWindow__buttons__action" v-on:click="createChannel = !createChannel"> CREATE Channel </button>                
            </div>
            <!-- /START BUTTONS -->
            <!-- CHAT -->
            <h1 class="chatWindow__header__chanel" v-if="chanelName" :class="{vievedTrue: !chat.viewed}"> {{chanelName}} </h1>
            <div class="chatWindow__chat" :class="{showChat: ShowChat}"  v-if="Channel" ref="chatWindow">
                <span class="chatWindow__chat__private" v-if="userToPrivateMessage == chat.user" v-on:click="userToPrivateMessage = null">
                        {{privMessage}}                         
                </span> <!-- privateMessage -->
                <div v-for="(messages,key) in Channel" :key="`${messages}-${key}`"  class="chatWindow__chat__row" :class="{'chatWindow__chat__row--your': messages.user === chat.user }" >
                    <span v-if="!messages.message.includes('private msg to')">
                        <span class="chatWindow__chat__row__message__user" v-if="chat.user != messages.user">{{messages.user}}</span> 
                        <span  class="chatWindow__chat__row__message__cloud" v-html="messages.message" v-on:click="resposneMessage(messages.message_id)" v-if="(!messages.message.title)"></span>
                        <span v-if="messages.message.title" class="chatWindow__chat__row__message__cloud" >
                            <b>{{messages.message.title}}</b> <br>
                            <img :src="messages.message.image" class="chatWindow__chat__row__message__cloud__image"> <br>
                            <span class="chatWindow__chat__row__message__cloud__description">{{messages.message.description}}</span>  <br>
                            <a :href="messages.message.url" target="blank" class="chatWindow__chat__row__message__cloud__a"> {{messages.message.url}}</a> 
                        </span> 
                        <span class="chatWindow__chat__row__message__cloud__emojiStart" v-on:click="addEmojiToMessage(messages.message_id, chat.user)"> &#128578; </span> <!-- EMOJI -->
                        <font-awesome-icon icon="check-circle"  class="chatWindow__chat__row__message__cloud__check-circle" :class="{vievedTrue: chat.viewed && Channel.length-1 == key}" /> <br>
                        <span  v-for="(emoji,key) in messages.reactions" :key="`${emoji}-${key}`" class="chatWindow__chat__row__message__reactions">
                             <span v-html="emoji.emojiName"> </span> {{emoji.user}} <br> 
                        </span>
                        <span class="chatWindow__chat__row__message__date" v-if="Channel.length-1 == key">{{messages.date}}</span>
                    </span>
                </div>
                 <!-- EMOJI REACTION-->
                <div class="emojiList" v-if="showEmojiList"> 
                    <span class="emojiList__emoji" v-on:click="choseEmoji(128516)"> &#128516; </span>
                    <span class="emojiList__emoji" v-on:click="choseEmoji(128525)"> &#128525; </span> 
                    <span class="emojiList__emoji" v-on:click="choseEmoji(128151)"> &#128151; </span> 
                    <span class="emojiList__emoji" v-on:click="choseEmoji(128580)"> &#128580; </span>
                    <span class="emojiList__emoji" v-on:click="choseEmoji(128577)"> &#128577; </span>
                    <span class="emojiList__emoji" v-on:click="choseEmoji(128077)"> &#128077; </span>
                    <span class="emojiList__emoji" v-on:click="choseEmoji(128078)"> &#128078; </span>  
                </div> 
                <!-- /EMOJI REACTION -->
                <!-- EMOJI IN MESSAGE-->
                <div class="emojiList" v-if="showEmojiListMessage"> 
                    <span class="emojiList__emoji" v-on:click="EmojiToMessage('')"> &#128516; </span>
                    <span class="emojiList__emoji" v-on:click="EmojiToMessage('')"> &#128525; </span> 
                    <span class="emojiList__emoji" v-on:click="EmojiToMessage('')"> &#128151; </span> 
                    <span class="emojiList__emoji" v-on:click="EmojiToMessage('')"> &#128580; </span>
                    <span class="emojiList__emoji" v-on:click="EmojiToMessage('')"> &#128577; </span>
                    <span class="emojiList__emoji" v-on:click="EmojiToMessage('')"> &#128077; </span>
                    <span class="emojiList__emoji" v-on:click="EmojiToMessage('')"> &#128078; </span>  
                </div> 
                <!-- /EMOJI IN MESSAGE -->
                <!-- INPUT  -->
                <input class="chatWindow__chat__input" type="text" v-model="chat.message" v-on:keyup.enter="sendMessage()" v-on:click="chat.viewed = true" > 
                <!-- /INPUT -->
                <!-- ADD IMG -->
                <div class="chatWindow__chat__addImg">
                    <image-uploader
                        :preview="false"
                        :className="['chatWindow__chat__addImg__fileinput', { 'chatWindow__chat__addImg__fileinput--loaded': hasImage }]"
                        capture="environment"
                        :debug="1"
                        :maxWidth="380"
                        :resize_enabled="true"
                        :resize_max_width="50"
                        :autoRotate="true"
                        outputFormat="verbose"
                        @input="setImage"
                    >
                    <label for="fileInput" slot="upload-label" class="chatWindow__chat__addImg__icon">
                        <font-awesome-icon icon="camera" title="Add image" v-if="!hasImage" :class="{fullWindowChat: fullWindowChat}"/> <br>
                    </label>
                    </image-uploader>
                    <span v-if="hasImage" style="color: #4fad73"> Image uploaded </span>
                    <span v-if="hasImage" style="color: #f00" v-on:click="DeleteImage()"> Delete </span>
                    <!-- ADD EMOJI TO MESSAGE -->
                    <span class="chatWindow__chat__addImg__addEmojiToMessage" v-on:click="showEmojiListMessage = !showEmojiListMessage"> &#128578; </span>
                    <!-- /ADD EMOJI TO MESSAGE -->  
                </div>
                 <!-- /ADD IMG -->
                <!-- RESPONSE -->
                <span v-if="MessageResposne.id" class="chatWindow__chat__response" > <b>Response to: </b> {{MessageResposne.user}} <br> <span v-html="MessageResposne.message"></span>
                <div class="chatWindow__chat__response__cancel" v-on:click="MessageResposne = {}"> X </div> </span>
                <!-- /RESPONSE -->
                  
                <button v-on:click="sendMessage()"   class="chatWindow__chat__send" > SEND  </button>
            </div>
            <!-- /CHAT -->
        </div>
        <!-- START ICON -->
        <div class="start_icon" v-on:click="openChat = true" v-if="!openChat" :class="{vievedTrue: !chat.viewed}">
            <font-awesome-icon icon="comment-dots"/>
        </div>
        <!-- /START ICON -->
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data(){
        return{ 
            url: [],
            showActions: false,
            showUsersList: false,
            showEmojiListMessage: false,
            fullWindowChat: false,
            showEmojiList: false,
            emoji: {
                user: null,
                message_id: null,
                emojiName: null, 
            },
            hasImage: false,
            image: null,
            ShowChat: false,
            openChat: false,
            joinChannel: false,
            createChannel: false,
            Channel: [],
            chat: {
                token: 'dupa',
                channel: null,
                user: null,
                create: null,
                message: [],
                noloader: true, 
                viewed: false,
                img: null,
            }, 
            chanelName: null,
            chatNameFromUrl: null,
            MessageResposne:{
                message: null,
                user: null,
                id: null
            },
            emailInvitation: {
                email: null,
                chanelName: null,
                url: null,
            },
            emailData: null,
            usersList: [],
            userToPrivateMessage: null,
            privMessage: null,
            
        }
    },
    methods:{
        privateMessage(user){
            this.userToPrivateMessage = user
            this.chat.message += 'private msg to ' + user + ' from '+ this.chat.user +':' 

        },
        async sendEmail(){
            const response = await axios.post("http://localhost:8003/sendInvitation.php", this.emailInvitation)
            this.emailData = response.data 
            if(this.emailData.status === 200){
                window.alert("Success! Email has been send.")
            } else {
                window.alert("Error, email has not been send. Correct your email.")
            }
        },
        EmojiToMessage(emoji){
            this.chat.message += emoji;
            this.showEmojiListMessage = false;
        },
        addEmojiToMessage(message_id, user){
            this.emoji.message_id = message_id;
            this.emoji.user = user;
            this.showEmojiList = true;
        },
        async choseEmoji(emojiName){
            this.emoji.emojiName = '&#'+emojiName+';'
            const response = await axios.post('http://webwizards.home.pl/jacek/chat-api/', {channel: this.chat.channel, message_id: this.emoji.message_id, reaction: this.emoji, token: this.chat.token})
            this.Channel = response.data.data
            this.showEmojiList = false
        },
        scrollDown(){
            setTimeout(() => {
                this.$refs.chatWindow.scrollTop = this.$refs.chatWindow.scrollHeight;
                this.chat.viewed = false
            }, 200);
        },
        DeleteImage(){
            this.hasImage = false
            this.image = null
        },
        async resposneMessage(id){
            this.Channel.forEach( e => { 
                if (e.message_id == id){
                    this.MessageResposne.message = e.message
                    this.MessageResposne.user = e.user
                    this.MessageResposne.id = id
                }    
            });
            this.scrollDown()

        },
        setImage: function(output) {
            this.hasImage = true;
            this.image = output;
        },
        async sharechanel(){
            const invateUrl = document.location.href +'?chat=' + this.chanelName 
            try {
              await navigator.clipboard.writeText(invateUrl);
              let email = prompt('Link is in clipboard. Write down email to send invitation link');
              this.emailInvitation.email = email
            } catch($e) {
              window.alert('Error, cannot copy');
            }
            this.emailInvitation.chanelName = this.chanelName
            this.emailInvitation.url = invateUrl
            this.sendEmail()
            
        },
        leavechanel(){
            this.joinChannel = false;
            this.createChannel = false; 
            this.ShowChat = false;
            this.chanelName = null;
            this.chatNameFromUrl = null;
            this.showUsersList = false;
            this.showActions = false;
        },
        async CreateChannel(){
            this.chanelName = this.chat.channel
            this.createChannel = !this.createChannel
            this.ShowChat = true
            const response = await axios.post('http://webwizards.home.pl/jacek/chat-api/', this.chat)
            this.Channel = response.data.data
            this.JoinChannel()
            this.scrollDown()

        },
        async JoinChannel(){
            this.joinChannel = !this.joinChannel
            this.ShowChat = true
            this.chanelName = this.chat.channel
            const response = await axios.post('http://webwizards.home.pl/jacek/chat-api/', {channel: this.chat.channel, all: true, token: this.chat.token})
            this.Channel = response.data.data
            this.showActions = true
            this.checkUersList()
            this.scrollDown()
        },
        isURL(str) {
          const pattern = new RegExp('^(https?:\\/\\/)?'+ // protocol
            '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|'+ // domain name
            '((\\d{1,3}\\.){3}\\d{1,3}))'+ // OR ip (v4) address
            '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*'+ // port and path
            '(\\?[;&a-z\\d%_.~+=-]*)?'+ // query string
            '(\\#[-a-z\\d_]*)?$','i'); // fragment locator
          return !!pattern.test(str);
        },

        async sendMessage(){
            if(this.chat.create){
                this.chat.channel = this.chat.create
                this.chat.create = null                
            }if (this.image){
               this.chat.message = '<img class="chatWindow__chat__row__message__cloud__img" src="'+this.image.dataUrl+'">'  
               this.image = null
               this.hasImage = false
            }
            if(this.chat.message == '' ){
                window.alert("You can't send empty message!")
                return
            }
            if(this.MessageResposne.id){
                this.chat.message = '<div class="chatWindow__chat__row__message__cloud__response">Response to: <b>' + this.MessageResposne.user +'</b> <br> '+ this.MessageResposne.message +'</div><br>  '  + this.chat.message 
                this.MessageResposne = {}
            }
            if(this.isURL(this.chat.message)){
                const response = await axios.get('http://webwizards.home.pl/jacek/website-info/', {params: {url: this.chat.message}})
                this.chat.message = response.data.data
            }

            const response = await axios.post('http://webwizards.home.pl/jacek/chat-api/', this.chat)
            this.Channel = response.data.data;
            this.chat.message = ''
            this.showEmojiListMessage = false;
            this.scrollDown()
        },
        async getMessage(){
            if(!this.chat.channel){
                return
            }

            const response = await axios.post('http://webwizards.home.pl/jacek/chat-api/', {channel: this.chat.channel, token: this.chat.token,noloader:true}).then((response) => {
                if(!this.Channel.find( e => e.message_id && e.message_id == response.data.data.message_id)){
                if(response.data.data.message.includes('private msg to ')){
                    this.usersList.forEach( e => {
                    let userToPrivateMessagee = response.data.data.message.substring(15, 19)
                    let pvMessage = response.data.data.message
                    if(e.includes(userToPrivateMessagee)){
                        this.userToPrivateMessage = e
                        this.privMessage = pvMessage
                        return;
                        }
                    })
                    return;
                }
                this.Channel.push(response.data.data)
                this.scrollDown()
                
            }  
            }) 
        },
        async joinInvitedChanel(){
            this.openChat = true
            this.joinChannel = true
            this.ShowChat = false
            this.chat.channel = this.chatNameFromUrl
            this.chanelName = this.chatNameFromUrl
            const response = await axios.post('http://webwizards.home.pl/jacek/chat-api/', {channel: this.chatNameFromUrl, all: true, token: this.chat.token})
            this.Channel = response.data.data
            this.scrollDown()
            this.checkUersList()
        },
        checkUersList(){
            if(this.Channel.length > 0){
                let users =  this.Channel.map( e => e.user ).filter((v, i, a) => a.indexOf(v) === i);
                this.usersList = users
            }
        }
    },
    mounted(){
        if(window.location.search){
            let params = (new URL(document.location)).searchParams;
            let chatNameFromUrl = params.get('chat');
            this.chatNameFromUrl = chatNameFromUrl
            this.joinInvitedChanel()
        }    
        setInterval(() => {
            this.getMessage()
        }, 1000);
        setInterval(() => {
            this.checkUersList()
        }, 5000);
    },
}
</script>

<style lang="scss">
.box{
    .emojiList{
        background-color: #4fad73;
        position: absolute;
        left: 13%;
        padding: 13px;
        border-radius: 1rem;
        top: 80%;
        display: flex;
        gap: 10px;
        &__emoji{
            font-size: 2rem;
        }
    }
    .chatWindow{
        z-index: 999;
        position: fixed;
        bottom: 2rem;
        right: 1rem;
        width: 500px;
        height: 650px;
        border: #30303de0 3px solid;
        background-color: #404053f6;
        transition: box-shadow 1s;
        &__usersList{
            font-size: 1.2rem;
            color: #f3f3f3;
            padding: 0.4rem;
            z-index: 999;
            background-color: #4fad73f1;
            border: 3px solid #3d885a;
            border-radius: 1rem;
            position: absolute;
            top: 8%;
            right: 5%;
            display: none;
            &.showUsersList{
                display: block;
            }
            &__user{
                transition: all .2s;
                &:hover{
                color: #2f43b1;
                }   
            }
        }
        .usersListButton{
            z-index: 99;
            position: absolute;
            right: 4%;
            background-color: #40405300;
            border: none;
            color: #f3f3f3;
            padding: 0.5rem;
            font-size: 1.5rem;
            cursor: pointer;
            transition: all 0.3s;
            &:hover{
                color: #4fad73;
            }
        }
        &.fullWindowChat{
            width: 98vw;
            height: 95vh;
            bottom: 0;
            right: 0;
            margin: 1rem;
        }
        &.vievedTrue{
        box-shadow: 0px 0px 24px 0px rgb(24 124 197);            
        }
        .leaveChanel{
            z-index: 99;
            position: fixed;
            margin-left: 5px;
            padding: 0.3rem;
            background-color: #40405300;
            border: none;
            color: #f3f3f3;
            padding: 0.5rem;
            font-size: 1.5rem;
            transform: rotateY(180deg);
            cursor: pointer;
            transition: all 0.3s;
            &:hover{
                color: rgb(207, 68, 68);
            }
        }
        .share{
            z-index: 99;
            position: fixed;
            margin-left: 38px;
            background-color: #40405300;
            border: none;
            color: #f3f3f3;
            padding: 0.5rem;
            font-size: 1.5rem;
            cursor: pointer;
            transition: all 0.3s;
            &:hover{
                color: #4fad73;
            }  
        }
        .expand{
            z-index: 99;
            position: fixed;
            margin-left: 75px;
            background-color: #40405300;
            border: none;
            color: #f3f3f3;
            padding: 0.5rem;
            font-size: 1.5rem;
            cursor: pointer;
            transition: all 0.3s;
            &:hover{
                color: #4fad73;
            }


        }
        &__chat{
            overflow-y: scroll;
            overflow-x: hidden;
            height: 87%;
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            display: none;
            &__private{
                z-index: 9999;
                padding: 1rem;
                background: #4fad73f3;
                color: #f3f3f3;
                position: absolute;
                left: 20%;
                top: 10%;
                border: 3px solid #d3d3d3;
                border-radius: 0.5rem;
            }
            &__response{
                background: #2f43b1ee;
                border-radius: 1rem;
                color: #d3d3d3;
                position: relative;
                width: auto;
                padding: 10px;
                position: absolute;
                bottom: 50px;
                left: 20px;
                img{
                    width: 80px;
                    height: auto;
                }
                &__cancel{
                    position: absolute;
                    top: -10px;
                    right: -10px;
                    color: #d3d3d3;
                    border: 2px solid #d3d3d3;
                    background-color: #30303de0;
                    border-radius: 50%;
                    width: 1.2rem;
                    height: 1.2rem;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    transition: all .4s;
                    &:hover{
                        background-color: #f00;
                    }
                }
            }
            &__addImg{
                position: absolute;
                bottom: 12px;
                right: 25%;
                &__fileinput{
                display: none;
                &--loaded{
                    display: none;
                }
            }
            &__addEmojiToMessage{
                position: absolute;
                bottom: 0;
                right: -30px;
            }
               
            }
            &__row{
                $t: &;
                padding: .5rem 1rem .5rem 2rem;
                position: relative;
                &__message{
                    width: 60%;
                    font-weight: 400;
                    &__reactions{
                        color: #d3d3d3;
                    }
                    &__cloud{
                        word-break: break-word;
                        display: inline-block;
                        color: #f3f3f3;
                        font-size: 1rem;
                        width: auto;
                        padding: .4rem .8rem;
                        border-radius: 1.5rem 1.5rem 1.5rem 0rem;
                        background-color: #449262;
                        &__image{
                            width: 150px;
                            height: auto;
                        }
                        &__description{
                            font-size: .7rem;
                        }
                        &__a{
                            color: #fff;
                            font-size: 1rem;
                            &:hover{
                                color: #d3d3d3;
                            }
                        }
                        &__emojiStart{
                            width: 1rem;
                        }
                        &__response{
                            display: block;
                            border-radius: 1rem;
                            padding: 8px;
                            font-size: 0.8rem ;
                            background-color:#2f43b1;
                            img{
                                width: 60px;
                                height: auto;
                            }
                        }
                        &__check-circle{
                        position: absolute;
                        bottom: 15px;
                        left: 6px;
                        opacity: 0;
                        img {
                            width: 50px;
                            height: auto;
                        }
                        &.vievedTrue{
                            opacity: 1;
                            color: rgba(255, 255, 255, 0.452);
                        }
                        &__img{
                            width: 50px;
                            height: auto;
                        }
                        }
                    }
                    &__user{
                        color: #d3d3d3;
                        display: block;
                        padding: .5rem;
                        font-size: .8rem;
                        font-style: italic;
                    }
                    &__date{
                        color: #777;
                        display: block;
                        padding: .5rem;
                        font-size: .8rem;
                        font-style: italic;
                        position: absolute;
                        width: 100%;
                        text-align: center;
                        left: 1rem;
                    }
                }
                &--your{
                    padding: .5rem 0 .5rem 2rem;
                    justify-content: flex-end;
