                border-bottom: 4px solid #d3d3d3;
                transition: all 0.4s;
                &.vievedTrue{
                    background:  rgb(24 124 197);
                    color: white;
                }
            }
        }
        &__joinChannel{
            display: none;
            &.showJoinChannel{
