                color: #000;
            }
            }
        }

    }
    .start_icon{
        z-index: 10;
        position: fixed;
        color: #f3f3f3;
        bottom: 4rem;
        right: 2rem;
        font-size: 5rem;
        transition: all 0.3s;
        transform: rotateY(180deg);
        &:hover{
            color: #4fad73;
            font-size: 5.5rem;
        }
        &.vievedTrue{
