<template>
<div class="backgroundd">
    <div class="window__wrap">
        <button class="close" v-on:click=" $emit('close', true)" v>X</button>
        <div class="windowScroll">
            <h1 class="windowScroll__h1">CHOSE YOUR SKILLS</h1>
            <div class="form">
                <input type="text" v-model="searchSkill" class="input" placeholder="Search skills">
                <button v-on:click="$emit('chosenSkills', selectedSkill)" class="confirm">
                    CONFRIM SKILLS
                </button>                
            </div>
            <div class="selecdedSkillsBox" v-if="selectedSkill !=  '' ">
                <div v-for="skill in selectedSkill" :key="skill">
                    <div class="selecdedSkills" v-on:click="deleteSkill(skill)">
                        {{skill}}
                    </div>
                </div>
            </div> <br>
            <div v-for="skill in filterSkills" :key="skill" class="skills" v-on:click="addSkills(skill)">
                <div>
                    {{skill}}
                </div>
            </div>    
        </div>
    </div>
</div>
</template>
<script>

export default {
    props: ['skills','UpdateUser'],
    data() {
        return {
            searchSkill: [],
            selectedSkill: []
        }

    },
    computed:{
        filterSkills(){
            return this.skills.filter( e => e.includes(this.searchSkill) || this.searchSkill == '')
        }
    },
    methods: {
        deleteSkill(skillToDelete){
            this.selectedSkill = this.selectedSkill.filter(e => e !== skillToDelete )
        },
        addSkills(skill){
            
        if(this.selectedSkill.some( e => e == skill)){
         return
        } else {
             this.selectedSkill.push(skill)
        }   
        },
        UpdateUserSkills(){
            Object.values(this.UpdateUser.selectedSkills).forEach( (e) => {
                this.selectedSkill.push(e)
            })
        },
    },
    mounted(){
        if(this.UpdateUser) {
            this.UpdateUserSkills()
        }
    }
}
</script>

 
 <style lang="scss" scoped>
 *::-webkit-scrollbar {
  background-color: #30303d;
}
*::-webkit-scrollbar-thumb {
  background: #4fad73; 
}
