        top: 15px;
        max-height: 90vh;
        padding: 10px;
        display: flex;
        flex-wrap: wrap;

        .skills{
            padding: 10px;
            margin-top: 10px;
            border: 4px solid #f3f3f3;
            color: #f3f3f3;
            margin-right: 10px;
            transition: all 0.4s;
            &:hover{
                background: #4fad73;
                cursor: pointer;
            }
        }
        .windowScroll {
        max-height: 90vh;
        overflow-y: scroll;
        display: flex;
        flex-wrap: wrap;
        width: 100%;
            &__h1{
                display: flex;
                justify-content: center;
