                border-bottom: 4px solid #d3d3d3;
                padding-bottom: 1.5rem;
            }
            .form{
                border: none;
                width: 100%;
                display: flex;
                gap: 5px;
                .confirm{
                border: 0;
                background-color: #4fad73;
                width: 20%;
                color: #fff;
                font-size: 1rem;
                transition: background .4s;
                &:hover{
                    background: #d3d3d3;
                }
                }
                .input{
                color: #f3f3f3;
                background: none;
                border: 4px solid #f3f3f3;
                width: 80%;
                font-size: 1.1rem;
                padding: 10px;
                }
            }

            .selecdedSkillsBox{
              display: flex;
              background-color: #fff;
              padding: 15px;
              width: 97%;
              flex-wrap: wrap;
              gap: 10px;
              .selecdedSkills{
                  background-color: #4fad73;
                  color: #fff;
                  padding: 15px;
                  font-size: 1rem;
                  font-weight: bold;
                  transition: background .4s;
                  &:hover{
                      background: rgba(245, 0, 0, 0.733);
                  }

              }
          }

        }
        .close {
        position: absolute;
