        right: -10px;
        border: 2px solid #000;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        font-weight: bold;
        cursor: pointer;
        &:hover{
            background-color: #ffd000;
        }
        }
    }
 }
    @media only screen and (max-width: 900px) {
