<template>
<div class="box">
    <div>
        <UploadImage v-on:uploadAvatar="users.avatar = $event " v-if="!close" v-on:close="close = $event"/>
    </div>
    <div class="form">
      <div class="form__inputs">
        <div class="form__allWithotAboutMe">
          <div class="form__inputs__basic">
            <span> Name  <br> <input type="text" placeholder="Name"  v-model="users.firstName"  :class="{'error':errors.includes('firstName')}" class="form__inputs__basic__input"></span>
            <span>Surname <br> <input type="text" placeholder="Surname" v-model="users.lastName" :class="{'error':errors.includes('lastName')}" class="form__inputs__basic__input"></span>
            <span>Phone <br> <input type="tel"  placeholder="Phone" v-model="users.phone" :class="{'error':errors.includes('phone')}" class="form__inputs__basic__input"> </span>
            <span>E-mail <br> <input type="email" placeholder="Email" v-model="users.email" :class="{'error':errors.includes('email')}" class="form__inputs__basic__input"> </span>
            <span> Github name <br><input type="text"  placeholder="Github name" v-model="users.githubName" :class="{'error':errors.includes('githubName')}" class="form__inputs__basic__input"> </span>
          </div>
          <div class="form__inputs__job">
            <span class="input_select">   City <br> <v-select :options="getCities" v-model="users.city" :class="{'error':errors.includes('city')}" class="v-select"> </v-select></span>
            <span class="input_select__title"> Title <br><v-select :options="getTitle" v-model="users.title" :class="{'error':errors.includes('title')}" class="v-select"> </v-select></span>
            <span class="input_select">Experience <br><v-select :options="getExperienceLevel" v-model="users.experience_level" :class="{'error':errors.includes('experience_level')}" class="v-select"> </v-select></span>
          </div> 
        </div>    
        <div class="form__inputs__about">
          <span >About me <br> <textarea cols="30" rows="5" v-model="users.aboutMe" :class="{'error':errors.includes('aboutMe')}"></textarea> </span>           
        </div>
      </div>    
      <div class="form__buttons">
        <span>
          <button v-on:click="$emit('skillsWindow', true)" class="buttons"> Add Skill</button>
        </span>
        <span>
          <button v-on:click="$emit('HistoryOfWorkWindow', true)" class="buttons"> Add history of work</button>
        </span>
        <span>
          <button v-on:click="close = false" class="buttons"> Upload image</button>
        </span>  
      </div>     
      </div>
    <div>
          <button v-on:click="checkForm" class="box__sendall">SEND</button >
    </div>
</div>
</template>

<script>

import axios from "axios"
import UploadImage from './UploadImage.vue'
export default {
  props:['UpdateUser','getCities','getTitle','getExperienceLevel', 'skills', 'selectedSkills' , 'jobHistory'],
  components: {
      UploadImage,
  },
  data() {
    return {
      skill: '',
      users: {
        avatar: null,
        firstName: null,
        lastName: null,
        phone: null,
        email: null,
        aboutMe: null,
        githubName: null,
        city: '',
        title: '',
        experience_level: '',
        selectedSkills: [],
        jobHistory: [],
      },
      errors: [],
      close: true,
    }
  },
  methods:{
    
    checkForm() {
      this.errors = []
       Object.keys(this.users).forEach( (key) => {
         if(['',null].includes(this.users[key])){
           this.errors.push(key)
           console.log(this.errors)
         }
       })
      if((this.errors.length == 0  || this.errors == 'avatar' ) && this.UpdateUser != ''  ){
        this.fetchUser(this.users.githubName).then( () => {
        this.$emit('update', {...this.users})}).catch(() => {
          window.alert('GitHub user not found'); 
        });
        return
      }

      if(this.errors.length == 0 || this.errors == 'github' || this.errors == 'avatar'){
          this.fetchUser(this.users.githubName).then( () => {
          this.$emit('add', {...this.users})  
        }).catch(() => {
            console.log(this.errors);
            window.alert('GitHub user not found');
              this.users.github = null;
              this.$emit('add', {...this.users})
              this.errors = [] 
         });
      }
    },

    async fetchUser(username){
      const response = await axios.get(`https://api.github.com/users/${username}`)
      if(response && response.data && response.data.login){
        this.users.github = response.data;
      }
      return;
    },

  },
  watch: {
    UpdateUser:{
      handler(UpdateUser){
        this.users = {...UpdateUser}
      },
      deep:true
    },
    selectedSkills(selectedSkills){
      this.users.selectedSkills = {...selectedSkills}
    },
    jobHistory(jobHistory){
      this.users.jobHistory = [...jobHistory]
    },

  } 
 
}
</script>

<style lang="scss">
