<template>
    <div v-if="user" class="block">
        <div v-if="user.github && !user.avatar" class="block__image">
            <img :src="user.github.avatar_url"> <br>
        </div>
        <div v-if="user.avatar" class="block__image">
            <img :src="user.avatar.dataUrl" >
        </div>
        <div class="block__data">
            <h2>{{user.firstName}} {{user.lastName}}</h2>
            <div class="block__contact">
                <div class="block__icon">
                    <font-awesome-icon icon="phone" />                
                </div>
                 <a :href="`tel:${user.phone}`">{{user.phone}}</a>      
            </div>
            <div class="block__contact">
                <div class="block__icon">
                    <font-awesome-icon icon="envelope" />
                </div>
                 <a :href="`mailto:${user.email}`">{{user.email}}</a>                
            </div>
        </div>
        <div class="block__data block__data--white">
            {{user.aboutMe}}                
        </div>
        <div v-if="user.github" class="block__data block__data--white">
            <div class="block__contact">
                <div class="block__icon">
                    <font-awesome-icon icon="link" />
                </div>
                <a :href="user.github.html_url" target="_blank" title="Click to see github account!"> {{user.github.login}} </a> 
            </div>
            <div class="block__contact">
                <div class="block__icon">
                    <h2>R</h2> 
                </div>
                 <span v-on:click="ReposWindowClick" title="Click to see repos!" class="block__contact__repos"> Repos: {{user.github.public_repos}}</span>                
            </div>
            <div class="block__contact">
                <div class="block__icon">
                    <h2>F</h2>
                </div>
                <span> Followers: {{user.github.followers}}</span>
            </div>
        </div>
        <div class="block__data block__data--white">
            <span> <b>Title:</b> {{user.title}}</span><br>
            <span> <b>City:</b> {{user.city}}</span><br>
            <span> <b>Experience:</b> {{user.experience_level}} </span><br>
            <span> <b> Job offers: </b>{{  user.offersCount }}</span> <br>
            <div class="skills">
                 <b>Skills:</b> 
                <span v-for="Skill in user.selectedSkills" :key="Skill" >
                    {{Skill}},
                </span>   
            </div> 
        </div>
            <div class="block__data__buttons">
                <span class="block__data__header" v-bind:class="{gray: user.jobHistory == ''}"> Career Summary</span> 
                <span :class="{upsidedown: isActive}" class="block__data__buttons__icon" v-if="user.jobHistory != ''">
                    <font-awesome-icon icon="angle-down" v-on:click="isActive = !isActive"  class="block__data__buttons__hs"/> 
                </span>
            </div>
            <span v-bind:class="{active: isActive}" class="block__data__allWorkHistory">
            <div v-for="work in user.jobHistory" :key="work" class="block__data block__data--white block__jobHisory" >
                <span> <b> Company: </b> {{work.company}} </span> <br>
                <span> <b> Job position: </b>  {{work.workplace}} </span> <br>
                <span> <b> Start of work </b> {{work.start}}  <div v-if="!work.present"> <b> end of work </b>{{work.end}}</div> <div v-if="work.present"> <b> to </b> present  </div> </span> 
                <span> <b> Outline: </b> <br> {{work.outline}}</span> <br>
                <span> <b> Key responsibilities: </b> <br> {{work.responsibilities}} </span> <br>
            </div>
            </span>    
        <div class="block__actions">    
            <button v-on:click="$emit('deleteUser', user.lastName)">
                Delete
            </button>
            <button v-on:click="$emit('setUpdateUser', user) && moveTo()">
                Update
            </button>
            <button v-on:click="$emit('sendJob', user)" v-bind:class="{gray: user.offers == ''}" class="block__actions__searchJob">
                Search job
            </button>
            <button v-on:click="$emit('userPDF', user)">
                Generate PDF
            </button>
            <button v-on:click="sendEmail()">
                Send e-mail
            </button>
        </div> 
    </div>
</template>
<script>
import axios from 'axios'
export default {
    props: ['user','UpdateData','UpdateUser'],
    data(){
        return{
            carierShow: true,
            isActive: false,
            emailData: null,
        }
    },
    methods: {
        async sendEmail(){
            const response = await axios.post("http://localhost:8002/send.php", this.user)
            this.emailData = response.data 
            if(this.emailData.status === 200){
                window.alert("Success! Email has been send.")
            } else {
                window.alert("Error, email has not been send. Correct your email.")
            }
        },
        ReposWindowClick () {
            this.$emit('repos', this.user.github.repos_url)
        },
        hideWorkHistory(){
            this.carierShow = null
        },
        moveTo () {
            window.scroll({
            top: 0,
            left: 0,
            behavior: 'smooth'
            })
            this.moveToDown = !this.moveToDown
        }
    },
}



</script>
<style lang="scss"  scoped>
.block {
    &__jobHisory{
        font-size: 1rem;
    }
    &__contact{
        color: #f3f3f3;
        display: flex;
        align-items: center;
        gap: 1rem;
        margin-top: 10px;
        a{
            color: #f3f3f3 !important;
        }
        &__repos{
            cursor: pointer;
            transition: all .15s;
            &:hover{
                color: #4fad73;
            }
        }
    }
    &__actions{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        &__searchJob{
            &.gray{
                background-color: #adadad;
            }
        }

        button {
            margin: .5rem;
            border: 0;
            background: #4fad73;
            padding: .5rem 1rem;
            font-size: 1rem;
            font-weight: bold;
            color: #fff;
            transition: background .2s;
            &:hover{
                background: #d3d3d3;
            }
        }
    }

    &__icon{
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0.5rem;
        font-size: 1.25rem;
        height: 1.5rem;
        width: 1.5rem;
        background-color: #4fad73;
        border-radius: 50%;
    }

    &__image{
        background-color: #30303de0;
        width: 150px;
        height: 150px;
        overflow: hidden;
        border-radius: 50%;
        position: absolute;
        top: -1rem;
        right:-1rem;
        border: 5px solid #d3d3d3;
        img{
            width: 100%;
        }
    }

    &__data{
        color: #f3f3f3;
        margin-bottom: 10px;
        padding: 0rem 1rem 1rem 1rem;
        h2{
            font-size: 1.2rem;
            color: #d3d3d3;
            margin: 1rem 0;
        }
        &__allWorkHistory{
            max-height: 0;
            overflow: hidden;
            transition: all 1.5s;
            &.active{
                max-height: 1000px;
            }
        }

        &__buttons{
        gap: 0.2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        border-bottom: 4px solid #d3d3d3;
        &__icon{
            position: absolute;
            right: 0.5rem;
            transition: all 0.4s;
            &.upsidedown{
                transform: rotate(180deg);
            }
        }

        &__hs{
            padding: 0.2rem 0.8rem;
            margin: .1rem;
            border: 0;
            background: #4fad73;
            font-size: 2rem;
            border-radius: 2rem;
            font-weight: bold;
            color: #fff;
            transition: all .2s;
            &:hover{
                background: #d3d3d3;
                }
            }

        }

        &--white{
            padding: 0rem 1rem 1rem 1rem;
            border-bottom: 4px solid #d3d3d3;
        }
        &__header{
            margin-right: auto;
            margin-left: auto;
            text-align: center;
            font-size: 1.5rem;
            font-weight: bold;
            background-color: #4fad73;
            width: 60%;
            padding: 0.75rem;
            margin-bottom: 10px;
            border-radius: 2.5rem;
            color: #fff;
            &.gray{
                background-color: #adadad;
            }
        }
    }
    font-family: monospace;
    position: relative;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 1);
    width: 400px;
    height: auto;
    background-color: #30303de0;;
    font-size: 1rem;
    display: flex;
    flex-direction: column;
    border-top-right-radius: 5rem;
    margin: 2rem 1rem;
    padding: 0.4rem;
    .aboutMe{
        font-size: 1rem;
    }
    a {
        text-decoration: none;
        color: black;
    }
    .user_avatar {
        height: 3rem;
        width: auto;
        border: 1px solid #000;
        position: absolute;
        top: 10px;
        right: 10px;
        border-radius: 0.5rem;
    }
    button {
        margin-top: 10px;
        font-size: 1rem;
        padding: 0.5rem;
        margin-top: 0.75rem ;
        width: 10rem;
        cursor: pointer;
        margin-bottom: 0.75rem;
    }
    .del_button{
        width: 60px;
        position: absolute;
        bottom: 2px;
        right: 10px;
        font-size: 0.75rem;
    }
    .update_button {
        width: 60px;
        position: absolute;
        bottom: 2px;
        left: 10px;
        font-size: 0.75rem;
    }
    .jobHistory{
        margin: 10px 0;
        font-size: 1rem;
    }
}
 @media only screen and (max-width: 900px) {
     .block{
         margin-left: auto;
         margin-right: auto;
         width: 65%;
     }
 }

</style>