<template>
    <div class="backgroundd">
        <div class="window__wrap">
            <button class="close" v-on:click="$emit('close', true)" v>X</button>
            <div class="main">
                <image-uploader
                    :preview="true"
                    :className="['main__fileinput', { 'main__fileinput--loaded': hasImage }]"
                    capture="environment"
                    :debug="1"
                    doNotResize="gif"
                    :autoRotate="true"
                    outputFormat="verbose"
                    @input="setImage"
                >
                <label for="fileInput" slot="upload-label" class="upload-caption--icon">
                    <font-awesome-icon icon="camera" v-if="!hasImage"/> <br>
                  <span class="upload-caption">{{
                    hasImage ? "Replace"  : "Click to upload"
                  }}</span>
                </label>
                </image-uploader>
                <span class="confirm" v-if="hasImage" v-on:click="$emit('uploadAvatar', image) && $emit('close', true)">
                    Confirm 
                </span>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    data(){
        return {
            hasImage: false,
            image: null,
        }
    },
    methods:{
        setImage: function(output) {
            this.hasImage = true;
            this.image = output;
        },
    },

}

</script>

<style lang="scss">

 *::-webkit-scrollbar {
  background-color: #30303d;
}
*::-webkit-scrollbar-thumb {
  background: #4fad73; 
}
 .backgroundd{
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.651);
    width: 100vw;
    height: 100vh;
    z-index: 10;
    .window__wrap{
        margin-top: 6rem;
        z-index: 10;
        display: block;
        position: fixed;
        width: 60%;
        background-color: #30303de0;
        border: 2px solid #000;
        left: 50%;
        transform: translateX(-50%);
        top: 15px;
        max-height: 90vh;
        padding: 10px;
        display: flex;
        flex-wrap: wrap;
        min-height: 70vh;
        .close {
        position: absolute;
        top: -10px;
        right: -10px;
        border: 2px solid #000;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        font-weight: 900;
        cursor: pointer;
            &:hover{
                background-color: #4fad73;
            }
        }
        .main{
            padding: 1rem;
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            color: #f3f3f3;
            font-size: 3rem;
            text-align: center;
            .confirm{
                width: 100%;
                border-top: 4px solid #d3d3d3;
                padding: 1rem;
                padding-top: 1.3rem;
                color: #4fad73;
                transition: all 0.4;
                &:hover{
                    color: #3a7c53;
                }
            }
            &__fileinput{
                display: none;
                &--loaded{
                    display: none;
                } 
            }
            &__img-preview{
                max-width: 100px;
                max-height: 100px;
            }
            .upload-caption{
                font-size: 2rem;
                color: #4fad73;
                transition: all 0.4;
                cursor: pointer;
                &:hover{
                    color: #3a7c53;
                }
                &--icon{
                    cursor: pointer;
                    font-size: 6rem;
                    color: #4fad73;
                    &:hover{
                        color: #3a7c53;
                    }
                }
                
            }

        }
    }
 }
</style>
