<template>
    <div>
        <div class="form" v-if="workHistory">
            <span v-if="Company"> <span class="text">Select your company </span> <br> <v-select  :options="Company" v-model="workHistory.company" class="vselect" ref="company_selected_ref"  @search="company_name($event)" > </v-select> </span> 
            <span> <span class="text">Job position</span>  <br> <input type="text" v-model="workHistory.workplace" class="inputForm"> </span> 
            <span> <span class="text">Start of work</span>  <br> <input type="date" v-model="workHistory.start" class="inputForm"> </span> 
            <span class="text__presentButton"> <span class="text"  v-if="!workHistory.present">End of work</span>  <br> <input v-if="!workHistory.present" type="date" v-model="workHistory.end" class="inputForm--end"> <span class="text__present"> Present  <input type="checkbox" v-model="workHistory.present"></span></span> 
            <span> <span class="text">Outline</span>  <br> <textarea cols="50" rows="6" v-model="workHistory.outline" placeholder="Olutline" class="textarea"></textarea> </span> 
            <span> <span class="text">Key responsibilities</span>  <br> <textarea cols="50" rows="10" v-model="workHistory.responsibilities" placeholder="Key responsibilities" class="textarea"></textarea> </span> 
        </div>
    </div>
</template>

<script>

export default {
    data (){
        return {
            workHistory: {},
            company_selected: null,
            Company: [],
        }
    },
    props:['formData','index', 'UpdateUser', 'workHistoryUpdate', 'getCompany'],
    onChange:{

    },
    watch:{
        workHistory: {
            handler(){
                if(this.workHistory.present == true){
                    this.workHistory.end = null
                }
                this.fillForm()
            },
            deep:true
        },

    },
    methods:{
        company_name(event){
            if (event != ''){
                let temp = [...this.Company]
                temp[0] = event
                this.Company = []
                this.Company = temp            
            }


        },
        fillForm(){ 
            if(this.workHistory.start > this.workHistory.end){
                window.alert("Wrong date")
                this.workHistory.end = null
                return;
            }
            this.$emit('updateHistory' , {data:{...this.workHistory} ,index: this.index})   
        },
    },
    mounted(){  
        this.Company = this.getCompany 
        let index = this.index
        this.workHistory = {...this.workHistoryUpdate[index]}
        
        }
}
</script>

<style lang="scss" >
    .form{
        margin-left: 2rem;
        min-width: 620px;
        display: flex;
        flex-direction: column;
        padding: 20px;
        font-size: 1.5rem;
        .nameOfCompany{
            width: 20rem;
        }
        .vselect{
            width: 21.5rem;
            background-color: none;
        }
        .textarea {
            background-color: none;
            resize: none;
            font-size: 1rem;
        }
        .inputForm{
            width: 20rem;
            margin-top: 10px;
            margin-bottom: 8px;
            color: #d3d3d3;
            border: 4px solid #d3d3d3;
            background: none;
            padding: 8px;
            font-size: 1rem;
            &--end{
                margin-top: 10px;
                margin-bottom: 8px;
                color: #d3d3d3;
                border: 4px solid #d3d3d3;
                background: none;
                padding: 8px;
                font-size: 1rem;
                width: 15rem;
            }
        }
        .confirm{
            margin: 10px 0;
            font-size: 1.2rem;
            padding: 15px 50px;
            cursor: pointer;
        }
        .text{
            background-color: none;
            padding: 10px 20px 10px 10px;
            color: white;
            font-size: 1.3rem;
            &__present{
                top: 10px;
                padding: 12px;
                background-color: #4fad73;
                color: #fff;
            }
            &__presentButton{
                margin-bottom: 25px;
            }
            
        }
    }
@media only screen and (max-width: 1100px) {
    .form{
        min-width: 500px;
        display: flex;
        flex-direction: column;
        padding: 10px;
        font-size: 1.2rem;
        .nameOfCompany{
            width: 16rem;
        }
        .vselect{
            width: 21.5rem;
            background-color: none;
        }
        .textarea {
            background-color: none;
            resize: none;
            font-size: 1rem;
        }
        .inputForm{
            width: 20rem;
            margin-top: 10px;
            margin-bottom: 8px;
            color: #d3d3d3;
            border: 4px solid #d3d3d3;
            background: none;
            padding: 8px;
            font-size: 1rem;
            &--end{
                margin-top: 10px;
                margin-bottom: 8px;
                color: #d3d3d3;
                border: 4px solid #d3d3d3;
                background: none;
                padding: 8px;
                font-size: 1rem;
                width: 15rem;
            }
        }
        .confirm{
            margin: 10px 0;
            font-size: 1.2rem;
            padding: 15px 50px;
        }
    }   
}
</style>
