<template>
  <div class="app">
      <div class="loading"><div></div><div></div><div></div><div></div></div>
      <div>
        <Chats />
      </div>
      <div>
        <UserPdf :userPdfData="userPdfData" v-if="userPdfData != ''" v-on:close="userPdfData = ''"/>
      </div>
      <div>
        <HistoryOfWork :getCompany="getCompany" :UpdateUser="UpdateData" v-if="historyOfWorkPopup" v-on:close="historyOfWorkPopup = false" v-on:workHistory="addWorkHistoryToForm($event)"/>
      </div>   
      <div>
        <SkillsWindow :UpdateUser="UpdateData" v-if="skillsWindowPopup" :skills="skills" v-on:chosenSkills="addSkillsToForm($event)" v-on:close="skillsWindowPopup = false"/>  
      </div>
      <div>
        <ReposWindow :giturl="giturl" v-if="giturl" v-on:close='giturl=null'/>
      </div>
      <div>
        <OffersWindow :getOffers="getOffers(job)" :user="user" v-if="job" :job="job" v-on:close='job=null' />
      </div>  
      <InputText :UpdateUser="UpdateData" v-on:update="updateUserData($event)" v-on:add="addUser($event)" v-on:job="offersWindow($event)" :skills="skills" :getCities="getCities" 
      :getTitle="getTitle" :getExperienceLevel="getExperienceLevel" :selectedSkills="selectedSkills" v-on:skillsWindow="skillsWindowPopup = true" :jobHistory="jobHistory"
       v-on:HistoryOfWorkWindow=" historyOfWorkPopup = true"/> 
      <div class="user__stats">
        <span>Users: {{getStats.users}}</span>
        <span>Users with github: {{ getStats.githubusers }}</span> 
        <span>Job offers: {{ getStats.offersCount }}</span>
      </div>
      <div class="users_block">
        <UserItem :user="user" v-for="(user,key) in getUsers" :key="`${user.lastName}-${key}`" v-on:repos="reposWindow($event)" 
        v-on:sendJob="job = $event" v-on:deleteUser="deleteUser($event)" v-on:setUpdateUser="setUpdateUser($event)" :UpdateData="UpdateData" :UpdateUser="UpdateUser" v-on:userPDF="userPDF($event)" />
      </div>
      <span v-if="!users">brak danych</span>
  </div>
</template>

<script>

import skills from './assets/skills.json'
import InputText from './components/InputText.vue'
import UserItem from './components/UserItem.vue'
import ReposWindow from './components/ReposWindow.vue'
import OffersWindow from './components/OffersWindow.vue'
import SkillsWindow from './components/SkillsWindow.vue'
import { LMap, LTileLayer, LMarker } from 'vue2-leaflet'
import HistoryOfWork from './components/HistoryOfWork.vue'
import HistoryOfWorkForm from './components/HistoryOfWorkForm.vue'
import UserPdf from './components/UserPdf.vue'
import UploadImage from './components/UploadImage.vue'
import Chats from './components/Chats.vue'


import axios from "axios" 

export default {

  components: { 
    InputText,  
    UserItem,
    ReposWindow,
    OffersWindow,
    LMap,
    LTileLayer,
    LMarker,
    SkillsWindow,
    skills,
    HistoryOfWork,
    HistoryOfWorkForm,
    UserPdf,
    UploadImage,
    Chats,
  },
  data(){

    return {
      avatar: '',
      users: [],
      reops: [],
      giturl: null,
      offers: [],
      job: null,
      UpdateUser: null,
      UpdateData: [],
      skills: [],
      selectedSkills: [],
      skillsWindowPopup: false,
      historyOfWorkPopup: false,
      jobHistory: [],
      userPdfData: []
    }
  },
  computed:{
    getStats() {
      return {
        users: this.users.length,
        githubusers: this.users.filter( e => e.github).length,
        offersCount: this.getUsers.map(e => e.offersCount ).reduce((e, count) => count + e,0)
      }
    },
    getCities(){
      return this.offers.map( e => e.city ).filter((v, i, a) => a.indexOf(v) === i);
    },
    getTitle() {
      return this.offers.map( e=> e.title ).filter((v, i, a) => a.indexOf(v) === i);
    },
    getExperienceLevel () {
      return this.offers.map( e=> e.experience_level ).filter((v, i, a) => a.indexOf(v) === i);
    },
    getOffers(){ return user => this.offers.filter(e => e.title === user.title && e.city === user.city)},
    getCompany(){
      return this.offers.map( e => e.company_name).filter((v, i, a) => a.indexOf(v) === i);
    },
    getUsers() { return this.users !== null? this.users.map( user => ({
          ...user,
          offers: this.getOffers(user),
          offersCount: this.getOffers(user).length
        })): null
    }

  },
  methods: {
    userPDF(user){
      this.userPdfData = user
    },
    addWorkHistoryToForm(jobHistory){
      this.jobHistory = jobHistory
      this.historyOfWorkPopup = false
    },
    addSkillsToForm(selectedSkills){
      this.selectedSkills = selectedSkills
      this.skillsWindowPopup = false
    },
    deleteUser(lastName){
    this.users = this.users.filter( e => e.lastName != lastName)
    }, 
    updateUserData (updateUserData) {
      this.users = this.users.map( e => e.id == updateUserData.id? updateUserData : e);
      this.selectedSkills = this.selectedSkills.map (e => e.id == updateUserData.id? updateUserData: e)
      this.UpdateData = ''
    },
    reposWindow(giturl){
      this.giturl = giturl
    },
    offersWindow(job){
      this.job = job
    },
    setUpdateUser(UpdateData){
      this.UpdateData = UpdateData
    },
    addUser (user) {
      if(this.users.some( e => e.lastName == user.lastName)){
        alert("User with this lastname exist")
        return
      }
      this.users.push({...user,id:Math.random()})
      },
      async fetchOffers(){
        const response = await axios.get(`https://webwizards.home.pl/jacek/jjit/`);
        if(response.data){
          this.offers = response.data;

        }else{
          window.alert('Error fetching offers!')
        }
      },
      
    },
    mounted(){
      this.skills = skills;
      this.fetchOffers();
    }
  }

</script>

