<template>
    <div class="window__wrap">
        <div class="window">
            <div v-if="getOffers.length === 0" class="sorry">  
                <h1>Sorry, no job for people with your education</h1> <img src="../ferdek.jpeg" alt="" srcset="">
            </div>
            <button class="close" v-on:click=" $emit('close', true)" v>X</button>
            <div class="windowScroll">
                <h1 class="windowScroll__h1" v-if="getOffers.length != 0">JOB OFFERS</h1>
                <div v-for="ofert in getOffers" :key="ofert.id" class="box">
                    <div class="offer">
                        <div class="offer__logo">
                            <img :src="ofert.company_logo_url" class="company_logo"> <br>
                        </div>
                        <div class="offer__col">
                            <h1 class="offer__title">{{ofert.title}}</h1>
                            <div class="offer__data">
                                <a :href="ofert.company_url" target="_blank" class="link"> <strong>Company: {{ofert.company_name}}</strong> </a> <br>
                                <span> Address {{ofert.address_text}} </span> <br>
                                <span v-if="ofert.remote_interview"> Rremote interview: Yes </span>
                                <span v-if="ofert.remote_interview === false"> Rremote interview: No </span>
                            </div>
                        </div>
                        <div class="offer__col__experience">
                            <span> Experience level: {{ofert.experience_level}}  </span> <br>
                            <span v-for="money in ofert.employment_types" :key="money.type"> Employment type {{money.type}}  <br>
                                <span v-if="money.salary"> Salary from {{money.salary.from}} to  {{money.salary.to}} {{money.salary.currency}} </span> <br>
                            </span>
                        </div>
                        <div class="offer__col">
                            <div class="offer__skill" v-for="skills in ofert.skills" :key="skills.name">
                                <div class="offer__skill__name">
                                    {{skills.name}}
                                </div>
                                <div  class="offer__skill__stars">
                                    <span v-for="star in 5" :key="star" :class="[{'offer__skill__stars__star--selected':skills.level >= star},'offer__skill__stars__star']" >   <font-awesome-icon icon="star" /> </span>
                                </div>
                            </div>
                        </div >
                        <div class="offer__col__map">
                             <l-map style="height: 300px" :zoom="14" :center="[ofert.latitude, ofert.longitude]">
                               <l-tile-layer :url="'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png'" :attribution="''"></l-tile-layer>
                               <l-marker :lat-lng="[ofert.latitude, ofert.longitude]"></l-marker>
                             </l-map>
                        </div>
                    </div> 
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default ({
    props: ['getOffers'],
})
</script>

<style lang="scss" scoped>
*::-webkit-scrollbar {
  background-color: #30303d;
}
*::-webkit-scrollbar-thumb {
  background: #4fad73; 
}
.offer{
    padding-bottom: 2rem;
    background: none;
    border-bottom: 4px solid #f3f3f3;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    color: #f3f3f3;
    &__logo{
        margin-left: 3rem;
        flex: 1;    
        min-width: 100px;
        padding: 2rem;
        img{
            max-width: 100%;
        }
    }
    &__title{
        font-size: 2rem;
        margin: 0 0 1rem 0;
    }
    &__data{
        
        font-size: 1rem;
        margin: 1rem 0;
    }
    &__col{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 0.5rem 3rem;
        flex: 2;
        &__map{
            margin-right: 2rem;
            min-width: 300px;
            border: 2px solid #000;
        }

    }
    &__skill{
        padding: .25rem;
        &__name{
            font-size: .8rem;
            margin-bottom: .25rem;
        }
        &__stars{
            display: flex;
            justify-content: flex-start;
            max-width: 100px;
            &__star{
                color: #777;
                margin: .2rem;
                &--selected{
                    color: #ffd000;
                }
            }

        }
    }
}

.window__wrap{
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #000000bd;
    z-index: 9999;
}
.window {
    z-index: 10;
    display: block;
    position: fixed;
    width: 80%;
    background-color: #30303de0;
    border: 2px solid #000;
    padding: 10px;
    position: relative;
    .windowScroll {
        max-height: 90vh;
        overflow-y: scroll;
            &__h1{
            display: flex;
            justify-content: center;
            width: 100%;
            color:#4fad73;
            border-bottom: 4px solid #d3d3d3;
            padding-bottom: 1.5rem;
            }
    }
    .sorry{
        text-align: center;
        padding: 10px;
        color: #f3f3f3;
    }

    .close {
        position: absolute;
        top: -10px;
        right: -10px;
        border: 2px solid #000;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        font-weight: 900;
        cursor: pointer;
        &:hover{
            background-color: #4fad73;
        }
    }

    .box {
        padding: 10px;
        margin-top: 10px;
        display: flex;
        align-items: center;   
    }
    a {
        font-size: 1.25rem;
        text-decoration: none;
        color: #f3f3f3;;
        &.link:hover{
        color: rgb(112, 99, 99);
        text-decoration: underline;
        }
    }


}
@media only screen and (max-width: 1100px) {
.offer{
    padding: 40px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    &__title{
        width: 250px;
        font-size: 1.5rem;
        margin: 0 0 1rem 0;
        text-align: center;

    }
    &__data{
        width: 250px;
        font-size: 1rem;
        margin: 1rem 0;
        text-align: center;
    }
    &__col{
        padding: 0 1rem;
        flex: 1;
    }
    &__skill{
        padding: .25rem;
        &__name{
            font-size: .8rem;
            margin-bottom: .25rem;
        }
        &__stars{
            display: flex;
            justify-content: center;
            max-width: 100px;
            &__star{
                color: #777;
                margin: .2rem;
                &--selected{
                    color: #ffd000;
                }
            }

        }
    }
}
}
@media only screen and (max-width: 965px) {
.offer{
    text-align: center;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    gap: 20px;
    &__logo{
    flex: 1;    
    min-width: 100px;
    padding: 2rem;
    img{
        max-width: 100%;
    }
    }
    &__title{
        font-size: 2rem;
        margin: 0 0 1rem 0;
    }
    &__data{
        font-size: 1rem;
        margin: 1rem 0;
    }
}    
}
</style>
