<template>
    <div class="background">
        <div class="window"  v-if="giturl">
            <button class="close" v-on:click="$emit('close', true)">X</button>
                <div v-if="filterRepos == ''">
                    <h1 class="window__noRepos">This user has no repositories</h1>
                </div>
            <div v-if="filterRepos != ''" class="windowScroll">
                <h1 class="windowScroll__h1">REPOSITORIES</h1>
                <input type="text" v-model="search" placeholder="Search">
                <ul>
                    <li v-for="repoItem in filterRepos" :key="repoItem.id" class="items">
                        <a :href="repoItem.html_url" target="_blank" >{{ repoItem.name }}</a>
                        <p> {{repoItem.description}}</p>
                    </li>
                </ul>
            </div>
        </div>
    </div>    
</template>

<script>
import axios from "axios"


export default ({
    props: ['giturl'],


    data () {
        return{        
            repoContent: [],
            search: ''
        }


    },
    computed: {
        filterRepos(){
            return this.repoContent.filter( e => e.name.includes(this.search) || this.search == '')
        }
    },

    methods:{    
        async fetchReop(giturl){
            const response = await axios.get(giturl)
            if(response){
            this.repoContent = response.data;
            }
            return;
        },

    },
    mounted() {
        this.fetchReop(this.giturl)
    },   


})
</script>

<style lang="scss" scoped>
*::-webkit-scrollbar {
  background-color: #30303d;
}
*::-webkit-scrollbar-thumb {
  background: #4fad73; 
}
 .background{
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.808);
    width: 100%;
    height: 100vh;
    z-index: 10;
    .window {
        z-index: 10;
        display: block;
        position: fixed;
        width: 80%;
        background-color: #30303de0;
        border: 2px solid #000;
        left: 50%;
        transform: translateX(-50%);
        top: 15px;
        max-height: 90vh;
        padding: 10px;
        &__noRepos{
            color: #f3f3f3;
            text-align: center;
        }
        a {
            text-decoration: none;
            font-size: 1.5rem;
            color: #000;
            color: #5fd88e;
            transition: all 0.2s;
            &:hover{
                color: #36744e;
            }
        }
        p {
            color: #f3f3f3;
            font-size: 1rem;

        }
        .items {
            color: #f3f3f3;
            border-bottom: 3px solid #f3f3f3;
            padding: 10px;
            margin-top: 10px;
            list-style: none;
            width: 90%;
        }
        .windowScroll {
            max-height: 90vh;
            overflow-y: scroll;
            &__h1{
                display: flex;
                justify-content: center;
                width: 100%;
                color:#4fad73;
                border-bottom: 4px solid #d3d3d3;
                padding-bottom: 1.5rem;
            }

        }
        input {
            color: #f3f3f3;
            padding: 10px;
            width: 87.4%;
            margin-left: 40px;
            font-size: 1.5rem;
            margin-top: 10px;
            margin-bottom: 10px;
            background: none;
            border: 3px solid #f3f3f3;
        }
    }
    .close {
        position: absolute;
        top: -10px;
        right: -10px;
        border: 2px solid #000;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        font-weight: 900;
        cursor: pointer;
        &:hover{
            background-color: #4fad73;
        }
    }
 }
</style>
