<template>
<div class="backgroundd">
    <div class="main" >
        <button class="close" v-on:click=" $emit('close', true)" v>X</button>
        <button class="generate" v-on:click="generate()">  GENERATE PDF</button>
        <div ref="pdf_content" class="pdf_content">
            <div class="windowScroll" >
                <div class="left">
                    <div class="profile_photo" v-if="userPdfData.avatar === null">
                        <img :src="GitHubAvatar" class="profile_photo__img">
                    </div>
                    <div class="profile_photo" v-if="userPdfData.avatar != null">
                        <img :src="userPdfData.avatar.dataUrl" class="profile_photo__img">
                    </div>
                    <div class="aboutMe">
                        <h3> ABOUT ME</h3>
                        <p> {{userPdfData.aboutMe}} </p>
                    </div>
                    <div class="skills">
                        <h3>MY SKILLS</h3>
                            <span v-for="skills in userPdfData.selectedSkills" :key="skills"> - {{skills}} <br> </span>
                    </div>
                    <div class="github">
                         <h3>Github</h3> 
                         <a :href="userPdfData.github.html_url" target="_blank"> {{userPdfData.github.login}} </a>
                    </div>
                </div>
                <div class="right">
                    <div class="top">
                        <div class="top__left">
                            <span class="top__left__name"> {{userPdfData.firstName}} <br> {{userPdfData.lastName}} </span> <br> <br>
                            {{userPdfData.title}} 
                        </div>
                        <div class="top__right">
                            <span class="top__right__icons">
                                <div class="block__icon">
                                    <font-awesome-icon icon="phone" />
                                </div> 
                                {{ userPdfData.phone}}                                
                            </span> <br>
                            <span class="top__right__icons">
                                <div class="block__icon">
                                    <font-awesome-icon icon="envelope" />
                                </div>
                                {{userPdfData.email}}                           
                            </span>
                        </div>
                    </div>
                    <h1 class="work_expirience"> WORK EXPIRIENCE</h1> 
                    <div class="expirience">
                        <div v-for="work in userPdfData.jobHistory" :key="work" class="expirience__work" >
                            <div class="expirience__work__company">
                             <h2>{{work.company}}</h2>
                            {{work.start}} - <span v-if="!work.present"> {{work.end}}</span> <span v-if="work.present">present  </span>    
                            </div>
                            <div class="expirience__work__outline">
                                <span> <h2>{{work.workplace}}</h2>  </span>
                                <span> <h3> Outline </h3> {{work.outline}} </span>
                                <span> <h3> Key responsibilities </h3>  {{work.responsibilities}} </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import html2canvas from 'html2canvas';
import { jsPDF } from "jspdf";
import axios from "axios" 

export default {
    props: ['userPdfData'],
    data (){
        return{
            GitHubAvatar: null,
        }
    },
    methods:{
        generate(){
        var pageHeight = 2228
        var pageLeft = this.$refs.pdf_content.clientHeight 

        const doc = new jsPDF({
             orientation: 'p',
             unit: 'px',
             format: 'a4',
             hotfixes: ['px_scaling'],
        });
        var position = 0;
        var width = 1588
        
        html2canvas(this.$refs.pdf_content, {
        width: width,
        height: 2228
         }).then((canvas) => {
        const img = canvas.toDataURL("image/png");

        doc.addImage(img, "png", 0, position, width, pageHeight);
        pageLeft -= pageHeight/2;
        while (pageLeft >= 0){
        position =  position - pageHeight/2 + 10
        doc.addPage()
        doc.addImage(img, "png", 0, position, width, pageHeight);
        pageLeft -= pageHeight/2
        }
        doc.save(this.userPdfData.firstName + "_" + this.userPdfData.lastName + "_cv.pdf");
        })
        },
        async fetchAvatar(){ 
            const response = await axios.get(this.userPdfData.github.avatar_url, {responseType: 'arraybuffer'});
            if(response.data){
              this.GitHubAvatar ='data:image/jpeg;charset=utf-8;base64,'+ Buffer.from(response.data, 'binary').toString('base64');
            } else {
                console.log("avartar get errorr")
            }
        }        
    },
    mounted(){
        this.fetchAvatar();
    }
}
</script>

<style scoped lang="scss">
 .backgroundd{
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.808);
    width: 100%;
    height: 100vh;
    z-index: 10;
    .main{
        margin-top: 3rem;
        justify-content: space-around;
        display: block;
        position: fixed;
        border: 2px solid #000;
        left: 50%;
        transform: translateX(-50%);
        z-index: 99;
        background-color: white;
        .generate{
            color: white;
            font-weight: bold;
            background: #30303de0;
            padding: 1.2rem;
            font-size: 1.5rem;
            font-weight: bold;
            width: 100%;
            cursor: pointer;
            transition: all .3s;
            &:hover{
                background: #5b5b72e0;
            }
        }
        .close {
        position: absolute;
        top: -0.6rem;
        right: -0.6rem;
        border: 2px solid #000;
        border-radius: 50%;
        width: 2.2rem;
        height: 2.2rem;
        font-weight: 900;
        cursor: pointer;
        &:hover{
            background-color: #4fad73;
            }
        }
        .windowScroll{
            width: 21cm;
            height: auto;
            overflow-y: scroll;
            display: flex;
            .left{
                flex: 2;
                gap: 4rem;
                background: #4b4b4b;
                display: flex;
                flex-direction: column;
                color: #ebebeb;
                .skills{
                    font-size: 1rem;
                    margin-left: 1.2rem;
                    border-bottom: rgba(43, 42, 42, 0.589) solid 2px;
                    padding-bottom: 2.2rem;
                }
                .profile_photo{
                    margin-right: auto;
                    margin-left: auto;
                    margin-top: 1.5rem;
                    width: 70%;
                    height: 150px;
                    overflow: hidden;
                    border-radius: 50%;
                    &__img{   
                            width: 100%;
                    }
                }
                .aboutMe{
                    padding-bottom: 1.5rem;
                    margin-left: 1.2rem;
                    margin-right: 1.2rem;
                    font-size: 1rem;
                    border-bottom: rgba(43, 42, 42, 0.589) solid 2px;
                    h2{
                        font-size: 1.3rem;
                    }
                }
                .github{
                    margin-left: 1.2rem;
                    font-size: 1.2rem;
                    margin-bottom: 4rem;
                    a{
                        color: #ebebeb;
                        text-decoration: none;                        
                    }

                }
            }
            .right{
                background-color: #fff;
                flex: 5;
                color: #4b4b4b;
                .work_expirience{
                    margin-top: 1.2rem;
                    margin-left: 1.3rem;
                    font-size: 1.2rem;
                }
                .expirience{
                    margin-left: 1rem;
                    margin-right: 1rem;
                    &__work{
                        font-size: 0.8rem;
                        padding: 1rem;
                        border-bottom:  rgba(43, 42, 42, 0.589) solid 2px;
                        flex-direction: row;
                        display: flex;
                        &__company{
                            font-size: 0.7rem;
                            flex: 1;
                            border-right: rgba(43, 42, 42, 0.589) solid 2px;;
                        }
                        &__outline{
                            margin-left: 0.8rem;
                            flex: 2;
                            font-size: 0.7rem;
                        }
                    }
                }
                .top{
                    border-bottom: 3px solid #4b4b4b;
                    display: flex;
                    padding: 0.7rem 0.5rem 0.7rem 0.5rem;
                    margin-left: 0.7rem;
                    margin-right: 0.7em;
                    &__left{
                        padding: 0.7rem;
                        flex: 3;
                        font-size: 1.1rem;
                        &__name{
                            font-size: 1.5rem;
                            font-weight: bold;
                        }
                    }
                    &__right{
                        font-weight: bold;
                        font-size: 0.9rem;
                        flex: 2;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        &__icons{
                            align-items: center;
                            display: flex;
                            flex-direction: row;
                            gap: 1.2rem;
                        }
                            .block__icon{
                            color: #fff;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            padding: 0.2rem;
                            font-size: 1rem;
                            height: 1.5rem;
                            width: 1.5rem;
                            background-color: rgb(58, 155, 34);
                            border-radius: 50%;
                        }
                    }
                }
            }
        }
    }
 }
</style>