<template>
<div class="backgroundd">
    <div class="box">
        <button class="close" v-on:click="$emit('close', true)" v>X</button>
        <div class="windowScroll">
            <h1 class="windowScroll__h1">HISTORY OF WORK</h1>
            <HistoryOfWorkForm v-on:updateHistory="updateHistory($event)" :formData="start" v-for="(start,key) in allWorkHistory" :key="key" :index="key"
            :UpdateUser="UpdateUser" :workHistoryUpdate="workHistoryUpdate" :getCompany="getCompany"/>
            <div class="addNewForm">
            <span v-on:click="addNewHistory" class="addNewForm__plus"><font-awesome-icon icon="plus" /></span>              
            </div>
            <button class="confirmall" v-on:click="confirmall2()">
            Confirm all and send
            </button>
        </div>
    </div>
</div>
    
</template>
<script>

import HistoryOfWorkForm from './HistoryOfWorkForm.vue'

export default {
    props: ['UpdateUser', 'getCompany'],
    components: {
        HistoryOfWorkForm,
    },
    data(){
        return {
            workHistory:{
                workplace: null,
                company: null,
                start: null,
                end: null,
                present: false,
                outline: null,
                responsibilities: null,
                
            },
            allWorkHistory:[],
            errorsH: [],
            jobHistory: [],
            closes: false,
            workHistoryUpdate: []
        }
    },
    methods:{
        confirmall2(){
            this.$emit('close', true)
            this.$emit('workHistory', this.allWorkHistory)
        },
        addNewHistory() {
            if(!this.allWorkHistory){
                this.allWorkHistory = [this.workHistory]
                return
            } 
            this.allWorkHistory.push({...this.workHistory})

        },
        updateHistory(data){
            let temp = [...this.allWorkHistory];
            this.allWorkHistory = [];
            temp[data.index] = data.data;
            this.allWorkHistory = temp;
        },
        UpdateHistoryOfWork(){
            this.allWorkHistory = this.UpdateUser.jobHistory
            this.allWorkHistory.forEach( e => {
                this.workHistoryUpdate.push(e)
            })
        },
    },
    mounted(){

        if(this.UpdateUser.length > 0 || this.UpdateUser.id != null) {
            this.UpdateHistoryOfWork()
        } else {
            this.addNewHistory()
        }

    },
}
</script>

<style lang="scss" scoped>
*::-webkit-scrollbar {
  background-color: #30303d;
}
*::-webkit-scrollbar-thumb {
  background: #4fad73; 
}
.backgroundd{
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.808);
    width: 100%;
    height: 100%;
    z-index: 10;
    .box{
        justify-content: center;
        z-index: 20;
        display: block;
        position: fixed;
        width: 50%;
        background-color: #30303de0;
        border: 2px solid #000;
        left: 50%;
        transform: translateX(-50%);
        top: 15px;
        max-height: 90vh;
        padding: 10px;
        display: flex;
        .close {
        position: absolute;
        top: -10px;
        right: -10px;
        border: 2px solid #000;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        font-weight: 900;
        cursor: pointer;
        &:hover{
            background-color: #4fad73;
        }
        }
        .windowScroll {
        overflow-y: scroll;
        justify-content: center;
        width: 100%;
            &__h1{
            display: flex;
            justify-content: center;
            width: 100%;
            color:#4fad73;
            border-bottom: 4px solid #d3d3d3;
            padding-bottom: 1.5rem;
            }
            .confirmall{
            font-weight: bold;
            color: #fff;
            background-color: #4fad73;
            width: 100%;
            margin: 10px 0;
            font-size: 1.5rem;
            padding: 20px 20px;
            cursor: pointer;
            }
            .addNewForm{
                display: flex;
                justify-content: center;
                align-items: center;
                height: 4.5rem;
                width: 100%;
                &__plus{
                    border: none;
                    font-size: 2.5rem;
                    color: #fff;
                    background-color: #4fad73;
                    width: 3rem;
                    border: 2px solid #fff;
                    padding: 0.6rem;
                    text-align: center;
                }

            }
        }
    }
} 
@media only screen and (max-width: 1300px) {
    .backgroundd{
        .box{
            .windowScroll {
                .confirmall{
                font-size: 1.2rem;
                padding: 10px 10px;
                }
            }
        }
    }
}   

</style>
